                                            '__cdata': ' Målet er at du skal bli kjent med dine egenskaper, interesser og kompetanse og ta gode valg for utdanning, opplæring og arbeid.\n                      Ta kontakt med et <a href="http://www.vox.no/Karriereveiledning/offentlige-karrieresentre/"\n                           target="_blank">offentlig karrieresenter</a> og gjør en avtale. ',
